export const getElementsHeight = (id) => {
  console.log(document.getElementById(id).offsetHeight)
  return document.getElementById(id).offsetHeight;
}

const handleTreatmentClick = (treatmentsRef, i) => {
  const navbarHeight = getElementsHeight('navbar') 
    const element = treatmentsRef.current[i]
    const y = element.getBoundingClientRect().top + window.pageYOffset - (navbarHeight+10);

    window.scrollTo({top: y, behavior: 'smooth'});
    console.log(element)
  }

  export default handleTreatmentClick;